@font-face {
  font-family: geo;
  src: url("Geomatrix.c75bdef0.otf");
}

@font-face {
  font-family: geom;
  src: url("GeomatrixMedium.1c417639.otf");
}

@font-face {
  font-family: geob;
  src: url("GeomatrixBold.d94c2ca6.otf");
}

:root {
  --black-stete: #2b2b2b;
  --indigo: #4f46e5;
  --blue: #2563eb;
  --grey: #969696;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: clamp(8px, 1vw, 10px);
}

body {
  place-content: center;
  width: 100dvw;
  height: 100dvh;
  font-family: geo;
  line-height: 1.5;
  display: grid;
}

.container {
  flex-direction: column;
  width: 100dvw;
  height: 100dvh;
  display: flex;
  overflow: hidden;
}

.header {
  background-color: #87ceeb;
  background-image: url("pattern-bg-desktop.ed47582d.png");
  background-size: cover;
  flex-direction: column;
  flex-basis: 35dvh;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.header__title {
  letter-spacing: 2px;
  color: #fff;
  font-family: geob;
  font-size: 4rem;
}

.header__form {
  border-radius: 1rem;
  justify-content: center;
  justify-self: center;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 10rem;
  display: flex;
  position: relative;
  overflow: hidden;
}

.header__input {
  letter-spacing: 1px;
  border: none;
  border-radius: 1rem;
  width: 50rem;
  padding: 1.5rem;
  font-family: geob;
  font-size: 2rem;
}

.header__input::placeholder {
  letter-spacing: 0;
  opacity: .8;
  font-family: geo;
  font-size: 1.6rem;
}

.header__input:focus {
  outline: none;
}

@media (width < 400px) {
  .header__input {
    width: 40rem;
  }
}

.header__btn {
  background-color: var(--black-stete);
  color: #fff;
  cursor: pointer;
  border: none;
  padding: 2rem;
  transition: all .2s;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.header__btn:hover {
  background-color: #2b2b2be6;
}

.header__icon {
  font-size: 2rem;
  transform: scale(1.5);
}

.result {
  z-index: 9999;
  background-color: #fff;
  border: 1px solid #1e1b4b80;
  border-radius: 1rem;
  gap: 2rem;
  padding: 3rem;
  font-size: 2rem;
  list-style: none;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 50%;
  overflow: hidden;
  transform: translate(50%, 50%);
  box-shadow: 0 0 3rem 1rem #1e1b4b33;
}

@media (width < 740px) {
  .result {
    flex-direction: column;
    gap: 3rem;
    transform: translate(50%, 80%);
  }
}

.result li {
  flex-direction: column;
  width: 25rem;
  display: flex;
}

@media (width < 1100px) {
  .result li {
    width: 20rem;
  }
}

@media (width < 740px) {
  .result li {
    align-items: center;
    width: 35rem;
  }
}

.result li:not(:last-child) {
  border-right: 1px solid #96969680;
}

@media (width < 740px) {
  .result li:not(:last-child) {
    border-bottom: 1px solid #96969680;
    border-right: none;
  }
}

.result__title {
  color: var(--grey);
  text-transform: uppercase;
  letter-spacing: 2px;
  font-family: geom;
  font-size: 1.4rem;
}

.result__value {
  color: var(--black-stete);
  margin-bottom: 1rem;
  font-family: geob;
  font-size: 2.5rem;
}

@media (width < 1100px) {
  .result__value {
    font-size: 2rem;
  }
}

@media (width < 740px) {
  .result__value {
    font-size: 2.4rem;
  }
}

.result__flag {
  width: 5rem;
  height: 3rem;
  margin-top: .5rem;
}

@media (width < 740px) {
  .result__flag {
    margin: 1rem auto 0;
  }
}

.result__flag-image {
  object-fit: cover;
  height: 100%;
}

#map {
  background: linear-gradient(to right, #2463eb33, #4f46e533);
  flex-basis: 65dvh;
  animation: 1s linear infinite colorise;
}

@keyframes colorise {
  0% {
    background: linear-gradient(to right, #2463eb33, #4f46e533);
  }

  100% {
    background: linear-gradient(to right, #4f46e533, #2463eb33);
  }
}

.tx-up {
  text-transform: uppercase;
}

.pl {
  width: 10rem;
  height: 10rem;
  display: block;
}

.pl__arrows, .pl__ring-rotate, .pl__ring-stroke, .pl__tick {
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.pl__arrows {
  transform-origin: 16px 52px;
  animation-name: arrows42;
  transform: rotate(45deg);
}

.pl__ring-rotate, .pl__ring-stroke {
  transform-origin: 80px 80px;
}

.pl__ring-rotate {
  animation-name: ringRotate42;
}

.pl__ring-stroke {
  animation-name: ringStroke42;
  transform: rotate(-45deg);
}

.pl__tick {
  animation-name: tick42;
}

.pl__tick:nth-child(2) {
  animation-delay: -1.75s;
}

.pl__tick:nth-child(3) {
  animation-delay: -1.5s;
}

.pl__tick:nth-child(4) {
  animation-delay: -1.25s;
}

.pl__tick:nth-child(5) {
  animation-delay: -1s;
}

.pl__tick:nth-child(6) {
  animation-delay: -.75s;
}

.pl__tick:nth-child(7) {
  animation-delay: -.5s;
}

.pl__tick:nth-child(8) {
  animation-delay: -.25s;
}

@keyframes arrows42 {
  from {
    transform: rotate(45deg);
  }

  to {
    transform: rotate(405deg);
  }
}

@keyframes ringRotate42 {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(720deg);
  }
}

@keyframes ringStroke42 {
  from, to {
    stroke-dashoffset: 452px;
    transform: rotate(-45deg);
  }

  50% {
    stroke-dashoffset: 169.5px;
    transform: rotate(-180deg);
  }
}

@keyframes tick42 {
  from, 3%, 47%, to {
    stroke-dashoffset: -12px;
  }

  14%, 36% {
    stroke-dashoffset: 0;
  }
}

.spinner {
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.hidden {
  display: none;
}

